@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Source+Code+Pro:wght@300;400;600&display=swap');
*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
    --color-bg: #171713;
    --color-bg-varient: #1d2023;
    --color-primary: #F6C90E;
    --color-primary-varient: #F6C90E;
    --color-light: #ffffff;
    --color-light-varient: #d9d5d5;


    --transition: all 400ms ease;

    
    --container-width-lg:75%;
    --container-width-md:85%;
    --container-width-sm:90%;
}
html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    /* background-image: url('./assets/bagground.png');
    background-repeat:repeat; */
    color: var(--color-light);
    line-height: 1.7;

}
.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
}


section{
    
    margin-top: 6em;
    /* height: 100vh; */
}

section > h2,section > h5{
    text-align: center;
    color: var(--color-light-varient);
}
section > h2 {
    color: var(--color-primary);
    margin-bottom: 3em;
}
.text-light{
    color: var(--color-light-varient);
}

a{
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover{
    color: var(--color-light);
}

.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75em 1.2em;
    border-radius: 0.4em;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
    margin: 1.3em 0;
}

.btn:hover{
    background: var(--color-light);
    color: var(--color-bg);
    border-color: transparent;
    
}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}




@media screen and ( max-width:1024px )  {
    .container{
        width: var(--container-width-md);
    }

    section{
        margin-top: 6em;
    }
}

@media screen and ( max-width:600px)  {
    .container{
        width: var(--container-width-sm);
    }

    section > h2{
        margin-bottom: 2em;
    }
}